@Component({
  selector: 'app-panel-driver',
  templateUrl: './panel-driver.page.html',
  styleUrls: ['./panel-driver.page.scss'],
})
export class PanelDriverPage implements OnInit {

  map: Leaflet.Map;
  properties = [];

  acc = true;
  dec = false;
  his = false;
  datastorage: any;
  name : any;
  id: any=0;
  orders: any = [];
  declined: any = [];
  hist: any=[];
  oid: any;
  latitude: any = 0; //latitude
  longitude: any = 0; //longitude

  
  constructor(
    private router: Router,
    private loadingController: LoadingController,
    private toastController: ToastController,
    private alertController: AlertController,
    private accessProviders: AccessProviders,
    public navController: NavController,
    private activatedRoute: ActivatedRoute,
    private storage: Storage,
    private navParamService: NavparamService
  ) { }

  options = {
    timeout: 10000, 
    enableHighAccuracy: true, 
    maximumAge: 3600
  }; 
  
  
  ngOnInit() {
    this.activatedRoute.params.subscribe((data : any)=>{
      this.id = data.id;
      console.log(this.id)
     });
  
// const map = new Map({
//     basemap: "osm" //Reference to the base of the map
//    });

//    const view = new MapView({
//      container: "container", // Reference to the view div created 
//      map: map, // Reference to the map object created before the view
//     zoom: 13, // Sets zoom level based on level of detail (LOD)
//      center: [120.73539645247061, 14.02017305] // Sets center point of view using longitude,latitude
//    });


return new Promise(resolve => {
      let body = {
        aksi: 'get_orders_driver'
      }
      this.accessProviders.postData(body, 'get_orders.php').subscribe((res:any) => {
        if(res.success == true) {
          console.log(res.result)
          for(let datas of res.result){
            this.orders.push(datas);
          }   
          console.log(this.orders);
        } else {

        }
      }, (err) => {
        this.presentAlert('Timeout');
      });
    });
  }  
  
  doRefresh(event) {
    console.log('Begin async operation');
    
    setTimeout(() => {
      console.log('Async operation has ended');
      event.target.complete();
    }, 2000);
  }
  

  leafletMap() {
    this.map = new Leaflet.Map('map').setView([14.0221, 120.7284], 14);

    Leaflet.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
    }).addTo(this.map);

    const markPoint = Leaflet.marker([14.0221, 120.7284]);
    markPoint.bindPopup('<p>Tashi Delek - Bangalore.</p>');
    this.map.addLayer(markPoint);
    
  }

  openOrder(i){
    this.navParamService.setOrderID(i);
    console.log(this.navParamService.getOrderID());
    //this.map.remove();
    this.router.navigate(['/order-detail']);
  }


  accept(){
    this.acc=true;
    this.dec=false;
    this.his=false;
  }
  decline(){
    this.acc=false;
    this.dec=true;
    this.his=false;
  }
  history(){
    this.acc=false;
    this.dec=false;
    this.his=true;
  }
  ionViewWillEnter(){

    return new Promise(resolve => {
      let body = {
        aksi: 'get_history_driver'
      }
      this.accessProviders.postData(body, 'get_orders.php').subscribe((res:any) => {
        if(res.success == true) {
          console.log(res.result)
          for(let datas of res.result){
            this.hist.push(datas);
          }   
          console.log(this.hist);
        } else {

        }
      });
    });

  }

  ionViewDidEnter() {
    //this.leafletMap();
    console.log(this.id)
    if(this.id == 1){
      window.location.reload();
    }
    this.navParamService.clear();
    this.storage.get('storage_xxx').then((res)=>{
      console.log(res);
        this.datastorage = res;
        this.name = this.datastorage.first_name;
    });

    return new Promise(resolve => {
      let body = {
        aksi: 'get_decline_driver'
      }
      this.accessProviders.postData(body, 'get_orders.php').subscribe((res:any) => {
        if(res.success == true) {
          console.log(res.result)
          for(let datas of res.result){
            this.declined.push(datas);
          }   
          console.log(this.orders);
        } else {

        }
      });
    });


  }


  async logout(){
    const loader = await this.loadingController.create({
      message: 'Please wait',
    });
    loader.present();
    loader.dismiss();
    this.storage.clear();
    this.presentToast('Log out successfully');
    this.navController.navigateRoot(['/intro']);
  }

  async presentToast(a) {
    const toast = await this.toastController.create({
      message: a,
      duration: 1500,
      position: 'bottom'
    });
    toast.present();
  }  

  async presentAlert(a) {
    const alert = await this.alertController.create({
      header: a,
      backdropDismiss: false,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Okay',
          handler: () => {
            console.log('Confirm Okay');
          }
        }
      ]
    });
  }

}